import React, { Component } from 'react';
import TextField from 'material-ui/TextField';
import ImageResults from './ImageResults';
import SearchFilters from './Filters';
import { orange500, blue500 } from 'material-ui/styles/colors';

const styles = {
  floatingLabelStyle: {
    fontSize: 19,
    textAlign: 'right',
    width: '99%',
    // transform: 'scale(0.75) translate(-15px, -28px)',
    transformOrigin: 'right top 0px',
  },
  hintStyle: {
    fontSize: 22,
    textAlign: 'center',
    width: '100%',
    margin: '0px 0px 10px 0px',
  },
  inputStyle: {
    fontSize: 27,
    color: orange500,
    textAlign: 'center',
    width: '100%',
    margin: '0px 0px 20px 0px',
  }
};

class Search extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchText: '',
      amount: 12,
      order: 'popular',
      orientation: 'all',
      imageType: 'all',
      category: '',
      color: '',
      apiURL: 'https://pixabay.com/api',
      apiKey: '8913420-89429fef6031f24fd40903778',
      images: [],
      total: 0,
    };
    this.onSearchTextChange = this.onSearchTextChange.bind(this);
    this.onFiltersChange = this.onFiltersChange.bind(this)
  }

  fetchRequest() {
    return fetch(`${this.state.apiURL}/?key=${this.state.apiKey}&q=${this.state.searchText}&image_type=${this.state.imageType}&per_page=${this.state.amount}&order=${this.state.order}&orientation=${this.state.orientation}&category=${this.state.category}&colors=${this.state.color}&safesearch=true`
    )
    .then(response => response.json())
    .then(response => this.setState({ images: response.hits, total: response.total }))
  }

  newRequest() {
    if (this.state.searchText === '') {
      this.setState({ images: [], total: 0})
    } else {
      this.fetchRequest()
    }
  }

  onSearchTextChange(e) {
    const val = e.target.value;
    this.setState({
      [e.target.name]: val
    }, this.newRequest
    )
  }

  onFiltersChange(value, property) {
    this.setState({
      [property]: value
    }, this.newRequest
    )
  }

  render() {
    console.log(this.state);

    const filterValues = {
      amount: this.state.amount,
      order: this.state.order,
      orientation: this.state.orientation,
      imageType: this.state.imageType,
      category: this.state.category,
      color: this.state.color,
    };

    const imageResultsValue = {
      orientation: this.state.orientation,
      total: this.state.total,
    };

    let searchHint = '< type here... >';
    let searchTotalHits = this.state.total + ' hits'

    return (
      <div>
        <TextField 
          name='searchText'
          value={this.state.searchText}
          onChange={this.onSearchTextChange}
          floatingLabelText={ searchTotalHits }
          floatingLabelFixed={true}
          floatingLabelStyle={styles.floatingLabelStyle}
          hintText={searchHint}
          hintStyle={styles.hintStyle}
          fullWidth={true}
          inputStyle={styles.inputStyle}
          // style={styles.custom}
      />
        <SearchFilters 
        value={filterValues}
        onChange={this.onFiltersChange}
        />
        {this.state.images.length > 0 ? <ImageResults images={this.state.images} value={imageResultsValue}/> : null}
      </div>
    )
  }
}

export default Search


