import React, { Component } from 'react';
import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import Divider from 'material-ui/Divider';

const customStyle = {
  width: 200,
  margin: '0px 5px',
};

// Fix Divider from "marginLeft: 72" to 0
const dividerStyle = {
  root: {
    marginLeft: 0,
  }
};

class ImgType extends Component {
  render() {
    return (
      <SelectField
        name='imageType'
        className='SelectField'
        value={this.props.value}
        onChange={(e, index, value) => this.props.onChange(value, 'imageType')}
        floatingLabelText='Image type'
        style={customStyle}
      >
        <MenuItem value='all' primaryText='all' style={{ fontWeight: 'bold' }} />
        <Divider inset={true} style={dividerStyle.root} />
        <MenuItem value='photo' primaryText='photo' />
        <MenuItem value='illustration' primaryText='illustration' />
        <MenuItem value='vector' primaryText='vector' />
      </SelectField>
    )
  }
}

export default ImgType


