import React, { Component } from 'react'
import AppBar from 'material-ui/AppBar';

import Drawer from 'material-ui/Drawer';
import IconButton from 'material-ui/IconButton';
import NavigationMenu from 'material-ui/svg-icons/navigation/menu';
import NavigationClose from 'material-ui/svg-icons/navigation/close';
import Menu from 'material-ui/Menu';
import MenuItem from 'material-ui/MenuItem';
import Divider from 'material-ui/Divider';
import ActionHome from 'material-ui/svg-icons/action/home';
import ActionPageView from 'material-ui/svg-icons/action/pageview';
import ImageGridOff from 'material-ui/svg-icons/image/grid-off';
import ImageGridOn from 'material-ui/svg-icons/image/grid-on';
import ImageColorLens from 'material-ui/svg-icons/image/color-lens';
import ContentLink from 'material-ui/svg-icons/content/link';

import TextField from 'material-ui/TextField';
import ActionSearch from 'material-ui/svg-icons/action/search';

import { Toolbar, ToolbarGroup, ToolbarSeparator, ToolbarTitle } from 'material-ui/Toolbar';


class NavBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      drawerOpen : false
    };
  }

  handleMenuClick = () => {
    this.setState({
      drawerOpen: !this.state.drawerOpen
    })
  }

  render() {
    return (
      <div>
        <AppBar
          title='Find picture with Pixabay API'
          iconElementLeft={
            <IconButton>
              <NavigationMenu 
                onClick={this.handleMenuClick}
              />
            </IconButton>}
          // iconElementRight={
          //   <div>  
          //     <ActionSearch />
          //     <TextField
          //       name='searchText'
          //       // value={this.state.searchText}
          //       // onChange={this.onSearchTextChange}
          //       // floatingLabelText={searchTotalHits}
          //       // floatingLabelFixed={true}
          //       // floatingLabelStyle={styles.floatingLabelStyle}
          //       // hintText={searchHint}
          //     />
          //   </div>
          // }
        />
        {/* <Toolbar>
          <ToolbarGroup firstChild={true}>
            <ActionSearch />
            <TextField
              name='searchText'
            />
          </ToolbarGroup>
          <ToolbarGroup>
            <ToolbarTitle text="Options" />
          </ToolbarGroup>
        </Toolbar> */}


        <Drawer 
          open={this.state.drawerOpen}
          docked={false}
          onRequestChange={(val) => this.setState({ drawerOpen: val })}
        >
          <AppBar 
            title='FindPicture' 
            showMenuIconButton={false}
            iconElementRight={
              <IconButton>
                <NavigationClose
                  onClick={this.handleMenuClick}
                />
              </IconButton>}
          />
          <Menu>
            <MenuItem primaryText="Home" leftIcon={<ActionHome />} onClick={this.handleMenuClick} />
            <MenuItem primaryText="LandingPage" leftIcon={<ActionPageView />} onClick={this.handleMenuClick} />
            <Divider />
            <MenuItem primaryText="Grid on" leftIcon={<ImageGridOn />} onClick={this.handleMenuClick} />
            <MenuItem primaryText="Grid off" leftIcon={<ImageGridOff />} onClick={this.handleMenuClick} />
            <Divider />
            <MenuItem primaryText="Style: " leftIcon={<ImageColorLens />} onClick={this.handleMenuClick} />
            <MenuItem primaryText=": default" onClick={this.handleMenuClick}  />
            <MenuItem primaryText=": grey"  onClick={this.handleMenuClick} />
            <Divider />
            <MenuItem primaryText="PixabayAPI" leftIcon={<ContentLink />} onClick={this.handleMenuClick} />
          </Menu>
        </Drawer>
      </div>
    )
  }
}


// const NavBar = () => (
  
// );

export default NavBar;


