import React, { Component } from 'react';
import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import ImgOrientation from './SearchFilters/ImgOrientation';
import ImgCategory from './SearchFilters/ImgCategory';
import ImgColor from './SearchFilters/ImgColor';
import ImgQty from './SearchFilters/ImgQty';
import ImgType from './SearchFilters/ImgType';
import ColorChip from './SearchFilters/Chip/ColorChip';

const customStyle = {
  width: 200,
  margin: '0px 5px',
};

class SearchFilters extends Component {
  render() {
    return (
      <div>
        <ImgQty
          value={this.props.value.amount}
          onChange={(value, property) => this.props.onChange(value, property)}
        />
        <SelectField
          name='order'
          value={this.props.value.order}
          onChange={(e, index, value) => this.props.onChange(value, 'order')}
          floatingLabelText='Sort by'
          style={customStyle}
        >
          <MenuItem value='popular' primaryText='popular' />
          <MenuItem value='latest' primaryText='latest' />
        </SelectField>
        <ImgOrientation
          value={this.props.value.orientation}
          onChange={(value, property) => this.props.onChange(value, property)}
        />
        <ImgCategory
          value={this.props.value.category}
          onChange={(value, property) => this.props.onChange(value, property)}
        />
        <ImgColor
          value={this.props.value.color}
          onChange={(value, property) => this.props.onChange(value, property)}
        />
        <ImgType
          value={this.props.value.imageType}
          onChange={(value, property) => this.props.onChange(value, property)}
        />
        <ColorChip 
          value={this.props.value.color}
          onChange={(value, property) => this.props.onChange(value, property)}
        />
      </div>
    )
  }
}

export default SearchFilters 


