import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { GridList, GridTile } from 'material-ui/GridList';
import IconButton from 'material-ui/IconButton';
import ZoomIn from 'material-ui/svg-icons/action/zoom-in';
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';

// hook for correct paddingTop when opening Dialog with dynamic content:
const dialogStyle = {
  dialogRoot: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    paddingTop: 0
  },
  dialogContent: {
    position: 'relative',
    width: '80vw',
    maxWidth: 800,
    transform: 'translate(0px, 0px)' 
  },
  dialogBody: {
    padding: 2
  }
};

class ImageResults extends Component {
  state = {
    open: false,
    currentImg: '',
    currentArrInd: '', 
  }

  handleImgOpen = (img, index) => {
    this.setState({ open: true, currentImg: img.largeImageURL, currentArrInd: index});
    (this.props.value.orientation === 'vertical') ? dialogStyle.dialogContent.maxWidth = 500 : 800
  }

  handleImgClose = () => {
    this.setState({ open: false });
  }
  
  handleNextButton = (side) => {
    let nextArrInd;
    if (side == 'right'){
      nextArrInd = this.state.currentArrInd + 1
    } else {
      nextArrInd = this.state.currentArrInd - 1
    }

    if (( nextArrInd > -1 ) && ( nextArrInd < ( this.props.images.length ) ) ) {
      this.handleImgOpen(this.props.images[nextArrInd], nextArrInd) 
    } else {
      this.handleImgClose()
    }
  }  
  
  render() {
    let imageListContent;
    const { images } = this.props;

    if(images) {
      imageListContent = (
        <GridList cols={3}>
          {images.map((img, index) => (
            <GridTile
              key={img.id}
              title={img.tags}
              subtitle={<span>by <strong>{img.user}</strong></span>}
              actionIcon={
                <IconButton onClick={() => this.handleImgOpen(img, index)}>
                  <ZoomIn color='white' />
                </IconButton>
              }
            >
              <img src={img.webformatURL} alt='' />
              {/* <img src={img.largeImageURL} alt='' /> */}
            </GridTile>
          ))}
        </GridList>
      )
    } else {
      imageListContent = null
    }

    const actions = [
      <FlatButton
        label='Left'
        primary={true}
        onClick={this.handleNextButton}
      />,
      <FlatButton
        label='Right'
        primary={true}
        onClick={() => this.handleNextButton('right')}
      />, 
      <FlatButton
        label = 'Close'
        primary = {true}
        onClick = {this.handleImgClose}
      />
    ]

    return (
      <div>

        {imageListContent}
        <Dialog
          actions = {actions}
          modal = {false}
          open = {this.state.open}
          onRequestClose = {this.handleImgClose}
          contentStyle={dialogStyle.dialogContent}
          bodyStyle={dialogStyle.dialogBody}
          style={dialogStyle.dialogRoot}
          repositionOnUpdate={false}
        >
          <img src={this.state.currentImg} alt='' style={{ width: '100%' }} />
        </Dialog>
      </div>
    )
  }
}

ImageResults.propTypes = {
  images: PropTypes.array.isRequired
}

export default ImageResults


