import React, { Component } from 'react';
import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import Divider from 'material-ui/Divider';

const customStyle = {
  width: 200,
  margin: '0px 5px',
};

// Fix Divider from "marginLeft: 72" to 0
const dividerStyle = {
  root: {
    marginLeft: 0,
  }
};

class ImgCategory extends Component {
  render() {
    const categoryArray = ['fashion', 'nature', 'backgrounds', 'science', 'education', 'people',
    'feelings', 'religion', 'health', 'places', 'animals', 'industry', 'food', 'computer', 
    'sports', 'transportation', 'travel', 'buildings', 'business', 'music'];
    
    const MenuItemCategory = (
      categoryArray.map((val, id) => (
        <MenuItem 
          key = {val + id}
          value = {val}
          primaryText = {val}
        />
      ))
    );

    return (
      <SelectField
        name='category'
        className='SelectField'
        value={(this.props.value === '') ? 'all' : this.props.value }
        onChange={(e, index, value) => this.props.onChange(value, 'category')}
        maxHeight={300}
        floatingLabelText='Category'
        style={customStyle}
      >
        <MenuItem value='all' primaryText='all' style={{fontWeight:'bold'}} />
        <Divider inset={true} style={dividerStyle.root} />
        {MenuItemCategory}
      </SelectField>
    )
  }
}

export default ImgCategory


