import React, { Component } from 'react';
import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import Divider from 'material-ui/Divider';

const customStyle = {
  width: 200,
  margin: '0px 5px',
};

// Fix Divider from "marginLeft: 72" to 0
const dividerStyle = {
  root: {
    marginLeft: 0,
  }
};

class ImgOrientation extends Component {
  render() {
    return (
      <SelectField
        name='orientation'
        className='SelectField'
        value={this.props.value}
        onChange={(e, index, value) => this.props.onChange(value, 'orientation')}
        floatingLabelText='Orientation'
        style={customStyle}
      >
        <MenuItem value='all' primaryText='all' style={{ fontWeight: 'bold' }} />
        <Divider inset={true} style={dividerStyle.root} />
        <MenuItem value='horizontal' primaryText='landscape'/>
        <MenuItem value='vertical' primaryText='portrait' />
      </SelectField>
    )
  }
}

export default ImgOrientation


